.cd-timeline {
  margin: 30px 0 0 0;
  padding: 50px 0 0 0;
  position: relative;
}

.cd-timeline:before {
  content: "";
  margin: 0 0 0 -2px;
  position: absolute;
  top: 0;
  left: 50%;
  width: 3px;
  height: 100%;
  background: #38909f;
}

@media (max-width: 720px) {
  .cd-timeline {
    margin: 0;
  }

  .cd-timeline:before {
    left: 31px;
  }
}

.cd-timeline .cd-timeline-block {
  margin: 30px 0;
  position: relative;
}

.cd-timeline .cd-timeline-block:after {
  content: "";
  display: table;
  clear: both;
}

.cd-timeline .cd-timeline-block:first-child {
  margin-top: 0;
}

.cd-timeline .cd-timeline-block:last-child {
