/* 1. Basic */
html {
  margin-right: 0 !important;
}

body {
  margin: 0;
  padding: 0;
  border: none;
  font-family: "Roboto", helvetica;
  font-size: 16px;
  color: #868686;
  background: #eeeeee;
  line-height: 20px;
  letter-spacing: 0.01em;
  font-weight: 300;
}

.page {
  position: relative;
  overflow: hidden;
  padding: 320px 0 0 0;
}

@media (max-width: 720px) {
  .page {
    padding: 202px 0 0 0;
  }
}

* {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
}

.clear {
  clear: both;
}

.mobile {
  display: none;
}

@media (max-width: 720px) {
  .mobile {
    display: block;
  }
}

.desctop {
  display: block;
}

@media (max-width: 720px) {
  .desctop {
    display: none;
  }
}

/* 2. Container */
.container {
  position: relative;
  background: #eeeeee;
}

.wrapper {
  position: relative;
  margin: 0 auto;
  max-width: 1100px;
}

@media (max-width: 1080px) {
  .wrapper {
    margin: 0 20px;
  }
}

@media (max-width: 420px) {
  .wrapper {
    margin: 0 10px;
  }
}

